// ********************
// SECTIONS
// ********************

.section {
    position: relative;

    &.py-40 {
        padding: 1.25rem 0;

        @include breakpointsUp(medium) {
            padding: 2.5rem 0;
        }

    }

    &.py-80 {
        padding: 2.5rem 0;

        @include breakpointsUp(medium) {
            padding: 5rem 0;
        }
    }

    &.py-120 {
        padding: 3.75rem 0;

        @include breakpointsUp(medium) {
            padding: 7.5rem 0;
        }
    }

    &.py-160 {
        padding: 5rem 0;

        @include breakpointsUp(medium) {
            padding: 10rem 0;
        }
    }

    &.py-240 {
        padding: 7.5rem 0;

        @include breakpointsUp(medium) {
            padding: 15rem 0;
        }
    }

}


.mb-10 {
    margin-bottom: 0.625rem;
}

.mb-20 {
    margin-bottom: 1.25rem;
}

.mb-40 {
    margin-bottom: 2.5rem;
}

.mb-80 {
    margin-bottom: 5rem;
}