body {
    display: flex;
    flex-direction: column;
    width: 100vw;
    background-color: $white;
    overflow-x: hidden;
}

main {
    position: relative;
    width: 100vw;
    flex-grow: 1;
    flex-shrink: 1;

    &::before {
        content: '';
        position: absolute;
        z-index: 50;
        top: 0; right: 0; left: 0;
        width: 100vw; height: 40%;
        background-color: $dark-gray;
    }

    > section {
        position: relative;
        z-index: 100;
    }

}

section#hero {
    display: grid;
    place-items: center;
    height: 100%;
    padding: 7.5rem 0;
}

header {
    display: flex;
    place-items: center;
    width: 100vw;
    padding: 2.5rem 0;
    flex-grow: 0;
    flex-shrink: 1;
}

.logo {
    width: 10rem; height: auto;
}

h1 {
    font-family: 'Kiona', sans-serif;
    font-size: 2.5rem;
    line-height: 1;
    font-weight: 400;
    color: $gold-200;
    text-align: center;
    text-transform: uppercase;
}

.hero__ribbon {
    position: relative;
    z-index: 100;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 4rem;
    padding: 0.5rem 0;
    background-color: $gold-500;
    color: $white;
    text-align: center;
}

.hero__grid {
    display: grid;
    grid-template-rows: repeat(2, minmax(0, 1fr));
    box-shadow: 0px 25px 50px -12px rgba(0,0,0,0.25);
}

.video {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    border: 0;
}

.chat {
    width: 100%; height: 100%;
}

.video-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
}

.hero__bottom {
    border: 1px solid $gray-100;
    background-color: $gray-100;
}

footer {
    display: grid;
    place-items: center;
    width: 100vw;
    padding: 1.25rem 0;
    flex-grow: 0;
    flex-shrink: 1;

    @include breakpointsUp(medium) {
        padding: 2.5rem 0;
    }

}